@import url(https://fonts.googleapis.com/css2?family=Rajdhani:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

html,
body,
#root {
  height: 100%;
}

body {
  background: #f5f5f5;
}

body,
input,
button {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  background: #f55;
}

.container {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.timer-container {
  width: 100%;
  max-width: 300px;
  display: flex;
  flex-direction: column;
  background-color: #fff;
  justify-content: center;
  align-items: center;
  box-shadow: 1px 5px 12px 5px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  padding: 5px;
  padding-bottom: 10px;
}

p {
  font-size: 60pt;
  font-weight: normal;
  font-family: Rajdhani, Arial, Helvetica, sans-serif;
  color: #e88;
}

.title {
  font-size: 10;
  color: #e88;
  font-weight: normal;
  font-family: Ubuntu, Arial, Helvetica, sans-serif;
  text-align: center;
}

.buttons-container {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.button {
  height: 44;
  background-color: #e44;
  text-align: center;
  font-family: Ubuntu, Arial, Helvetica, sans-serif;
  font-size: 15pt;
  font-weight: 500;
  color: #fff;
  border-width: 0px;
  border-radius: 2px;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 10px;
  padding-right: 10px;
  margin-left: 6px;
  margin-top: 6px;
  box-shadow: 0.5px 0.5px 5px 1px rgba(0, 0, 0, 0.2);

  cursor: pointer;
}

.button:hover {
  background-color: rgb(247, 179, 179);
}

.button > p {
  display: none;
}

